<template>
  <div
    ref="chart"
    class="container"
  ></div>
</template>
<script>
import HighStock from "highcharts/highstock";
export default {
  props: {
    chartData: {
      type: Object,
      default: []
    }
  },
  data() {
    return {};
  },
  mounted() {
    HighStock.setOptions({
      lang: {
        rangeSelectorZoom: "", //  'zoom' 
        months: [
          "1",
          "2",
          "3",
          "4",
          "5",
          "6",
          "71",
          "8",
          "9",
          "10",
          "11",
          "12"
        ]
      },
      navigator: {
        enabled: false,
        height: 0
      },
      rangeSelector: {
        inputEnabled: false,
        selected: 0,
        buttonTheme: {
          display: "block" // 
        }
      },
      plotOptions: {
        series: {
          showInLegend: false
        }
      },
      tooltip: {
        split: false,
        shared: false
      },
      credits: {
        enabled: false
      }
    });
    HighStock.each(this.chartData, d => {
      d.length = 2;
    });
    HighStock.stockChart(this.$refs.chart, {
      series: [
        {
          type: "spline",
          name: "",
          data: this.chartData
        }
      ]
    });
  }
};
</script>
<style lang="scss" scoped>
.container {
  width: 100%;
  height: 20.5rem;
}
</style>





